            placeholder="ÏûÖÎ†• ÌõÑ ÏóîÌÑ∞!" v-on:keyup.enter="addTodo">     -->
       <input v-model ="qna_userid" placeholder="Í∏ÄÏì¥Ïù¥" /><br><br>
       <input v-model ="qna_title" placeholder="Ï†úÎ™©" /><br><br>
       <textarea cols="50" rows="10" v-model ="qna_content" placeholder="ÎÇ¥Ïö©" /><br><br>
       
        <span class="addbutton" v-on:click="write">Ïûë ÏÑ±</span>
    </div>    
</template>

<script type="text/javascript">
import Constant from '../js/Constant'

export default {
    qna_userid : 'input-todo',
    data : function() {
        return { 
            todo : "",
            qna_userid : "",
            qna_title : "",
            qna_content: ""
            }
    },
    methods : {
        addTodo : function() {
                        
            this.$store.dispatch(Constant.ADD_TODO, { todo: this.todo });
            this.todo = "";
            this.$router.push('/list');
        },

        write(){
            this.$store.dispatch(Constant.ADD_TODO, { todo: this.todo, qna_userid: this.qna_userid,
            qna_title: this.qna_title, qna_content: this.qna_content });
           
            this.$router.push('/list');
         }
    }
}
</script>